.app {
  display: flex;
  margin: 50px;
  justify-content: space-evenly;
}

.mainCont {
  position: relative;
  width: 412px;
  height: 416px;
  background: radial-gradient(
    98.96% 98.96% at 50% 0%,
    #232a34 0%,
    #181e27 100%
  );
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
}

.starCont {
  position: absolute;
  left: 32px;
  top: 32px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #262e38;
}

.star {
  position: absolute;
  left: 32.65%;
  right: 83.28%;
  top: 32.54%;
  bottom: 83.62%;
}

h2 {
